<template>
	<div id="app">
		<div class="page-content" :class="{'page-content-tabbar': tabbarVisible}">
			<transition :name="transitionDirection" mode="out-in" @before-enter="afterEnter" appear>
				<router-view :key="$route.fullPath" @handlerTabbar="handlerTabbar" />
			</transition>
		</div>
		
		<Contact :style="{ marginBottom: tabbarVisible ? '49px' : 0 }" />
		<Tabbar v-if="tabbarVisible" />
	</div>
</template>

<script>
	import Contact from '@/components/Contact.vue'
	import Tabbar from '@/components/Tabbar.vue'

	export default {
		name: 'app',
		components: { Contact, Tabbar },
		data() {
			return {
				transitionDirection: 'slide-left-',
				tabbarVisible: JSON.parse(sessionStorage.tabbarVisible)
			}
		},
		methods: {
			handlerTabbar(res) {
				this.tabbarVisible = res;
			},
			afterEnter() {
				this.$root.$emit('scrollAfterEnter');
			}
		}
	}
</script>

<style lang="scss">
	@import './styles/base.scss';
	@import './styles/app.scss';
</style>
